import copy from 'copy-to-clipboard'
import React, { useEffect, useMemo, useState } from 'react'
import { useLocation } from 'react-router-dom'
import {
  ActionableInput,
  Button,
  PaddedContent,
  Select,
  useShowError,
  useShowSuccessMessage,
  ValidatedInput,
} from '@aserto/console-common'

import { BaseConfiguration, ConfigurationType } from '../../../common/types'
import { useFetchExternalConfig } from '../../api/externalConfig'
import {
  useConfiguration,
  useRemoveConfiguration,
  useUpdateConfiguration,
} from '../../api/internal'
import DeleteConfirmationModal from '../../components/DeleteConfirmationModal'
import ConfigurationAddressValidator from '../../lib/validation/ConfigurationAddressValidator'
import { useActiveConfigurationName } from '../../services/ActiveConfiguration'
import EmptyConfigs from '../EmptyConfigs'
import {
  ButtonsContainer,
  ConfigureContainer,
  ConfigureContainerButton,
  ConfigureDiv,
  InputContainer,
  SelectContainer,
} from './styles'

const AUTO_CONFIGURATION_TYPE = 'auto'
const MANUAL_CONFIGURATION_TYPE = 'manual'
const CENTRAL_DIRECTORY_CONFIGURATION_TYPE = 'central_directory'

const ConfigurationsSettings: React.FC = () => {
  const { data } = useConfiguration()
  const { configurationName: configName, setConfigurationName } = useActiveConfigurationName()
  const [configType, setConfigType] = useState<ConfigurationType>(AUTO_CONFIGURATION_TYPE)
  const [automaticAddress, setAutomaticAddress] = useState<string | undefined>('')
  const [isAutomaticGenerated, setIsAutomaticGenerated] = useState<boolean>(false)
  const [configuration, setConfiguration] = useState<BaseConfiguration>({
    authorizerServiceUrl: '',
    authorizerApiKey: '',
    directoryServiceUrl: '',
    directoryApiKey: '',
    directoryTenantId: '',
    directoryReaderServiceUrl: '',
    directoryWriterServiceUrl: '',
    directoryModelServiceUrl: '',
  })

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setConfiguration((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }))
  }

  const [showDeleteModal, setShowDeleteModal] = useState(false)
  const { data: configData, refetch } = useFetchExternalConfig(automaticAddress!, {
    enabled: false,
    onError: (error) => {
      showError(error)
    },
    onSuccess: () => {
      showMessage('Configuration generated successfully.')
    },
  })

  const location = useLocation()

  const showError = useShowError()
  const showMessage = useShowSuccessMessage()
  const onClickShowDeleteModal = () => {
    setShowDeleteModal(true)
  }

  const configFields = useMemo(() => {
    return data?.configs?.find((e) => e.name === configName)
  }, [configName, data?.configs])

  const configOptions = [
    { label: 'Automatic', value: AUTO_CONFIGURATION_TYPE },
    { label: 'Manual', value: MANUAL_CONFIGURATION_TYPE },
    { label: 'Central Directory', value: CENTRAL_DIRECTORY_CONFIGURATION_TYPE },
  ]

  const { mutate: updateConfiguration } = useUpdateConfiguration({
    onError: (e) => {
      showError(e)
    },
    onSuccess: () => {
      showMessage('Configuration updated successfully.')
    },
  })

  const { mutateAsync: removeConfiguration } = useRemoveConfiguration({
    onSuccess: () => {
      showMessage('Configuration deleted successfully.')
      const activeConfig = (data?.configs?.filter((data) => data.name !== configName) || [])[0]
        ?.name
      if (activeConfig) {
        setConfigurationName(activeConfig)
      } else {
        setConfigurationName('')
      }
    },
    onError: (error) => {
      showError(error)
    },
  })

  const onClickRemoveConfiguration = (configName: string) => {
    if (configName) {
      removeConfiguration(configName)
    }
  }

  const dataConfigs = useMemo(() => {
    return configData?.configs || []
  }, [configData?.configs])

  const isAutomaticAddressValid = useMemo(() => {
    return ConfigurationAddressValidator.isValid(automaticAddress!)
  }, [automaticAddress])

  useEffect(() => {
    if (configFields?.configurationType) {
      setConfigType(configFields?.configurationType as ConfigurationType)
    }
  }, [configFields?.configurationType, setConfigType])

  useEffect(() => {
    if (configFields) {
      setConfiguration(configFields)
    }
  }, [configFields, setConfiguration])

  useEffect(() => {
    if (configFields?.address) {
      setAutomaticAddress(configFields.address)
    }
  }, [, configFields?.address, setAutomaticAddress])

  useEffect(() => {
    if (dataConfigs.length > 0) {
      setConfiguration(dataConfigs[0])
      setIsAutomaticGenerated(true)
    }
  }, [dataConfigs, setConfiguration, setIsAutomaticGenerated])

  useEffect(() => {
    if (
      location.state?.configuration &&
      Object.keys(configFields || {})
        .sort()
        .toString() === ['name'].toString()
    ) {
      setConfigType(AUTO_CONFIGURATION_TYPE)
      setAutomaticAddress('')
      setIsAutomaticGenerated(false)
      setConfiguration(location.state?.configuration)
    }
  }, [
    location.state,
    setConfiguration,
    setConfigType,
    setAutomaticAddress,
    setIsAutomaticGenerated,
    configFields,
  ])

  useEffect(() => {
    if (configFields?.address) {
      setIsAutomaticGenerated(true)
    }
  }, [configFields?.address, setIsAutomaticGenerated])

  if (!data || !data.configs || data.configs.length === 0) {
    return <EmptyConfigs></EmptyConfigs>
  }

  const ManualFields = (
    <>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Directory Service Address"
          name="directoryServiceUrl"
          value={configuration.directoryServiceUrl}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Directory API Key"
          name="directoryApiKey"
          placeholder={
            configuration?.directoryApiKey
              ? '****************************************************************'
              : ''
          }
          shouldShowHideShowButton
          type="password"
          value={configuration.directoryApiKey}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Director Tenant ID"
          name="directoryTenantId"
          value={configuration.directoryTenantId}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Authorizer Service Address"
          name="authorizerServiceUrl"
          value={configuration.authorizerServiceUrl}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Authorizer API Key"
          name="authorizerApiKey"
          placeholder={
            configuration?.authorizerApiKey
              ? '****************************************************************'
              : ''
          }
          shouldShowHideShowButton
          type="password"
          value={configuration.authorizerApiKey}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Directory Reader Service Address"
          name="directoryReaderServiceUrl"
          value={configuration.directoryReaderServiceUrl}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Directory Writer Service Address"
          name="directoryWriterServiceUrl"
          value={configuration.directoryWriterServiceUrl}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <ActionableInput
          disabled={configType === AUTO_CONFIGURATION_TYPE}
          label="Directory Model Service Address"
          name="directoryModelServiceUrl"
          value={configuration.directoryModelServiceUrl}
          onChange={handleInputChange}
          onClickCopy={copy}
        />
      </InputContainer>
      <ButtonsContainer>
        <Button
          onClick={() =>
            updateConfiguration({
              name: configName,
              fields: {
                configurationType: configType,
                name: configName,
                address: automaticAddress,
                directoryServiceUrl: configuration.directoryServiceUrl,
                authorizerServiceUrl: configuration.authorizerServiceUrl,
                authorizerApiKey: configuration.authorizerApiKey,
                directoryApiKey: configuration.directoryApiKey,
                directoryTenantId: configuration.directoryTenantId,
                directoryReaderServiceUrl: configuration.directoryReaderServiceUrl,
                directoryWriterServiceUrl: configuration.directoryWriterServiceUrl,
                directoryModelServiceUrl: configuration.directoryModelServiceUrl,
              },
            })
          }
        >
          Save
        </Button>
        <Button variant="danger" onClick={onClickShowDeleteModal}>
          Delete
        </Button>
      </ButtonsContainer>
    </>
  )
  return (
    <PaddedContent>
      <InputContainer>
        <ActionableInput
          disabled
          label="Configuration Name"
          value={configName}
          onClickCopy={copy}
        />
      </InputContainer>
      <InputContainer>
        <SelectContainer>
          <p>Configuration Type:</p>
          <Select
            options={configOptions}
            style={{ width: '700px' }}
            value={configOptions.find((option) => option.value === configType) || configOptions[0]}
            onChange={(option) => {
              const cfgType = String(option?.value) as ConfigurationType
              if (cfgType === AUTO_CONFIGURATION_TYPE) {
                setConfiguration(configFields || {})
              }
              setConfigType(cfgType)
            }}
          />
        </SelectContainer>
      </InputContainer>

      <DeleteConfirmationModal
        redirectUrl="/"
        show={showDeleteModal}
        subject="Configuration"
        onClickRemove={() => {
          onClickRemoveConfiguration(configName!)
          setShowDeleteModal(false)
        }}
        onHide={() => setShowDeleteModal(false)}
      />
      <ConfigureDiv>
        {configType === AUTO_CONFIGURATION_TYPE ? (
          <ConfigureDiv>
            {configType === AUTO_CONFIGURATION_TYPE && (
              <ConfigureContainerButton>
                <ConfigureContainer>
                  <ValidatedInput
                    label="Configuration address"
                    placeholder="https://localhost:8080"
                    validator={ConfigurationAddressValidator}
                    value={automaticAddress}
                    onChange={setAutomaticAddress}
                    onClickCopy={copy}
                  />
                </ConfigureContainer>
                <ButtonsContainer>
                  <Button
                    disabled={!isAutomaticAddressValid}
                    onClick={() => {
                      refetch()
                    }}
                  >
                    Generate configuration
                  </Button>
                  {!configData?.success && !configFields?.address && (
                    <Button variant="danger" onClick={onClickShowDeleteModal}>
                      Delete
                    </Button>
                  )}
                </ButtonsContainer>
              </ConfigureContainerButton>
            )}
            {isAutomaticGenerated ? ManualFields : <></>}
          </ConfigureDiv>
        ) : configType === MANUAL_CONFIGURATION_TYPE ? (
          ManualFields
        ) : (
          <>
            <InputContainer>
              <ActionableInput
                label="Directory Service Address"
                name="directoryServiceUrl"
                value={configuration.directoryServiceUrl}
                onChange={handleInputChange}
                onClickCopy={copy}
              />
            </InputContainer>
            <InputContainer>
              <ActionableInput
                label="Directory API Key"
                name="directoryApiKey"
                shouldShowHideShowButton
                type="password"
                value={configuration.directoryApiKey}
                onChange={handleInputChange}
                onClickCopy={copy}
              />
            </InputContainer>
            <InputContainer>
              <ActionableInput
                label="Directory Tenant ID"
                name="directoryTenantId"
                value={configuration.directoryTenantId}
                onChange={handleInputChange}
                onClickCopy={copy}
              />
            </InputContainer>
            <ButtonsContainer>
              <Button
                disabled={
                  !configuration.directoryTenantId ||
                  !configuration.directoryServiceUrl ||
                  !configuration.directoryApiKey
                }
                onClick={() =>
                  updateConfiguration({
                    name: configName,
                    fields: {
                      configurationType: configType,
                      name: configName,
                      directoryServiceUrl: configuration.directoryServiceUrl,
                      directoryApiKey: configuration.directoryApiKey,
                      directoryTenantId: configuration.directoryTenantId,
                    },
                  })
                }
              >
                Save
              </Button>
              <Button variant="danger" onClick={onClickShowDeleteModal}>
                Delete
              </Button>
            </ButtonsContainer>
          </>
        )}
      </ConfigureDiv>
    </PaddedContent>
  )
}

export default ConfigurationsSettings
