import { useQuery, UseQueryOptions } from 'react-query'
import { useAsertoApiWithAnonymousAccess } from '@aserto/console-common'

import { BaseConfiguration } from '../../common/types'
import { QueryKeys } from '../types/local/general'
import { RpcStatus } from '../types/local/tenant'
type ConfigDataResponse = {
  success: boolean
} & { configs: BaseConfiguration[] }

export const useFetchExternalConfig = (
  url: string,
  options?: Omit<
    UseQueryOptions<ConfigDataResponse, RpcStatus, ConfigDataResponse, QueryKeys[]>,
    'queryKey' | 'queryFn' | 'retry' | 'staleTime'
  >
) => {
  const { get } = useAsertoApiWithAnonymousAccess(url)
  return useQuery([url as QueryKeys], () => get<ConfigDataResponse>({ path: '' }), {
    ...options,
    enabled: !!url && options?.enabled,
    retry: false,
    staleTime: Infinity,
  })
}
