import { Validator, Validity } from '@aserto/console-common'

// Must start with a-z, and must be alphanumeric (or dashes) 6-30 characters
const CONFIGURATION_ADDRESS_REGEXP = /^(https?:\/\/[a-zA-Z0-9.-]+:\d+(\/[a-zA-Z0-9_/]+)?)$/

const getValidityOf = (address: string): Validity => {
  if (CONFIGURATION_ADDRESS_REGEXP.test(address)) {
    return 'VALID'
  }
  return 'INVALID'
}

const isValid = (address: string) => getValidityOf(address) === 'VALID'

const ConfigurationAddressValidator: Validator<string> = {
  getValidityOf,
  isValid,
  ruleText: 'Should be formed of an address and a port from topaz. It can be either https or http.',
}

export default ConfigurationAddressValidator
